import React from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryImg} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

const NAME = "Miniatures"
export const MinisCallout = () => 
<BannerBlock className="Callout" path={NAME.toLowerCase()} name={NAME} background="/assets/Background1.jpg" photos={[{name:"Miniatures", url:"/assets/TrollsThumb.png"}]}>
    <p>Painted Miniatures of various brands and styles</p>
    <p>Most for sale and are good examples of how to gauge comission quality concerns</p>
</BannerBlock>

export const MinisGallery = () => 
<GalleryPage title="Miniatures">
    <GalleryImg srcHint="Trolls" alt="Trolls" price={"15-$50"}>
        <p>A collection of my Trollblood Hordes army, painted to high tabletop</p>
        <p>Many more and complete units available as well as solos etc</p>
    </GalleryImg>
    <GalleryImg srcHint="Vera" alt="Vera and the Wurms" price={40}>
        <p>Vera and the Worms</p>
        <p>A capsule adventure I am working on, and will post more completely soon</p>
    </GalleryImg>
    <GalleryImg srcHint="MercSolos" alt="Mercenary Solos" price={"25/e"}>
        <p>Mercenary Solos, painted to high tabletop standard.</p>
    </GalleryImg>
    <GalleryImg srcHint="PinkMercs" alt="Pink Mercenaries" price={100}>
        <p>Pink Mercenaries</p>
        <p>Part of my Breast Cancer Brawl army, one set of many, but a fantastic starter merc battle group</p>
    </GalleryImg>
    <GalleryImg srcHint="AngryElves" alt="Angry Elves" price={"35/e"}>
        <p>Retribution Warcasters from Warmachine, done up in a fall themed color palette.</p>
        <p>I have many other models for this army in various stages of completeness and will be adding more as they are complete</p>
    </GalleryImg>
    <GalleryImg srcHint="Archer" alt="Archer" price={"25"}>
        <p>Archer I painted up as part of commision recently. Basic tabletop paintjob, quick and fun.</p>
        <p>Includes diorama with lid</p>
    </GalleryImg>
</GalleryPage>
