import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const CalloutStyles = styled.section`
position: fixed;
top: 0.66rem;
right: -3.66rem;
padding: 1rem 5rem;
background: #0d1111cc;
transform: rotate(30deg);
z-index: 1;
h1{
    margin: auto;
}

`

export const CalloutButton = () =>
    <CalloutStyles>
        
            <h1 className="CallToAction">
            <Link to="/callToAction">Connect
            </Link></h1>
        
    </CalloutStyles>