import React, {useState,Fragment} from 'react';
import styled from 'styled-components';

export const GalleryItem = styled.section`
position: relative;
background: #68894D50;
flex: 2 0 44%;
max-width: 300px;
margin: 0.3rem;
display: flex;
flex-flow: column nowrap;
box-shadow: 4px 4px 5px 0px #2e251e80;
background: linear-gradient(180deg, #f2ffd620 60%, #f2ffd600 100%);



.ImageBox {
    position: relative;
    width: fit-content;
    margin: 0 auto;
    z-index: 1;
    
    img{
        margin-left: 2rem;
        z-index:2;
    }
}

.RibbonContainer {
    position: absolute;
    left: 0.8rem;
    top: 1.2rem;
    display: flex;
    flex-direction: column;
    z-index: -1;
    justify-content: flex-start;

    h3 {
        font-size: 0.66rem;
        position: relative;
        z-index: -1;
        background: darkslategrey;
        padding: 0.2rem;
        margin: 0.2rem 0;
        min-width: 5rem;
    }

    h3:hover {
        transform: translate(-2rem);
        transition: all 300ms;
    }


}

h2 {
    font-size: 1rem;
    margin: 0.33rem 1.3rem;
    text-align: center;
}

.Description{ 
    margin: 0 1.3rem;
}

.Modal {
    min-width: 100vw;
    min-height: 100vw;
    pointer-events: none;
    opacity: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;  
    background: #000000B0;
    overflow-y: scroll;

        img, iframe{
            max-width: 98vw;
            max-height: 98vh;
            min-width: 48vw;
            min-height: 48vh;
            width: auto;
            height: auto;
            padding-top: 2rem;
            display: block;
            margin: auto;
        }
    }
.Modal-enter{
        pointer-events: auto;
        opacity: 0;
        z-index: 10;
    }
.Modal-enter-active{
        pointer-events: auto;
        opacity: 1;
        transition: all 666ms;
        z-index: 10;
    }
.Modal-exit{
        opacity: 1;
        z-index: 0;
    }
.Modal-exit-active{
        pointer-events: none;
        opacity: 0;
        z-index: -1;
        transition: all 666ms;
    }
`

const PullTab = (props) => {
    let [pulled,setPulled] = useState(false);
    const handleClick = (event) => {
        setPulled(!pulled);
    }
    let combineProps = {...props,pulled,onTouchStart: handleClick}
    return <h3 {...combineProps}>{props.children}</h3>

}

export const GalleryImg = (props) => {
    let [hide,setHide] = useState(true);
    const handleHideClick = (event) => {
        setHide(!hide);
    }
    let combineProps = {...props,hide,toggleHide:handleHideClick}
    return <InnerGalleryImg {...combineProps} />
}

const InnerGalleryImg = ({srcHint,alt,buyPrice,copyPrice,commishPrice,children,hide,toggleHide}) =>
    <GalleryItem hidden={hide} key={srcHint}>
        <h2>{alt}</h2>
        <div className='ImageBox'>
            <img onClick={toggleHide} src={`/assets/${srcHint}Thumb.png`} alt={alt} />
            <div className="RibbonContainer">
            {buyPrice ? 
                <h3 className=".Buy"><span aria-label="Buy the original" role="image">ðŸ’Ž</span> ${buyPrice}</h3>
            : <Fragment />}
            {copyPrice ? 
                <h3 className=".Copy"><span aria-label="Buy a Print" role="image">ðŸ“¥</span> ${copyPrice}</h3>
            : <Fragment />}
            {commishPrice ? 
                <h3 className=".Commish"><span aria-label="Commission new project like this" role="image">ðŸŽ¨</span> ${commishPrice}</h3>
            : <Fragment />}
            </div>
        </div>
        <div className="Description">
            {children}
        </div>
        <div onClick={toggleHide} 
        className={`Modal 
        ${hide? "Modal-exit Modal-exit-active":"Modal-enter Modal-enter-active" }`}>
            <img src={`/assets/${srcHint}Large.png`} alt={alt} />
        </div>
        
    </GalleryItem>